import {INITIAL_STATE} from "../data/settings";

const SERVER_URL = `https://es.dump.academy/pixel-hunter`;
const APP_ID = 322232;
const DEFAULT_NAME = INITIAL_STATE.userName;

export default class Loader {

  static async checkResponse(response) {
    if (response.ok && response.status === 200) {
      return response;
    }
    throw new Error(`${response.status}: ${response.statusText}`);
  }

  static async loadData(url) {
    const response = await fetch(url);
    await this.checkResponse(response);
    return response.json();
  }

  static async loadGameData() {
    return await this.loadData(`${SERVER_URL}/questions`);
  }

  static async saveResults(data, name = DEFAULT_NAME) {
    const initOptions = {
      body: await JSON.stringify(data),
      headers: {
        'Content-Type': `application/json`
      },
      method: `POST`
    };
    const response = await fetch(`${SERVER_URL}/stats/${APP_ID}-${name}`, initOptions);
    return await this.checkResponse(response);
  }

  static async loadResults(name = DEFAULT_NAME) {
    return await this.loadData(`${SERVER_URL}/stats/${APP_ID}-${name}`);
  }
}
