const hash = window.location.hash.replace(`#`, ``);

const AnswerType = {
  WRONG: `wrong`,
  CORRECT: `correct`,
  FAST: `fast`,
  SLOW: `slow`,
};

/**
 * object settings rules of the game
 * @type @readonly {Object}
 * @prop {boolean} debug              - to use debug mode add #debug to url, and see console
 * @prop {number} spinnerRotationTime - spinner rotation time after the load data from server, in ms (intro screen)
 * @prop {number} levels              -   ()
 * @prop {number} gameTime            -       
 * @prop {number} tickTime            -   , in ms
 * @prop {number} warningTime         -        // todo
 * @prop {number} slowTime            - slow answer time
 * @prop {number} quickTime           - fast answer time
 * @prop {number} lives               - -  (-  )
 * @prop {number} liveBonus           -     
 * @prop {Object.<string, number>} answersPoints - -   
 */
const RULES = Object.freeze({
  debug: hash.toLowerCase() === `debug`,
  spinnerRotationTime: 50,
  levels: 10,
  gameTime: 30,
  tickTime: 1000,
  warningTime: 5,
  slowTime: 20,
  quickTime: 10,
  lives: 3,
  liveBonus: 50,
  answersPoints: {
    [AnswerType.WRONG]: 0, //   
    [AnswerType.CORRECT]: 100, //       (quickTime >   < slowTime)
    [AnswerType.FAST]: 150, //      ( < quickTime )
    [AnswerType.SLOW]: 50 //      ( > slowTime )
  }
});

/**
 * initial game settings
 * @type @readonly {Object}
 * @prop {string} userName              -    
 * @prop {number} lives                 - -    
 * @prop {number} time                  - -    
 * @prop {number} currentLevel          -  
 * @prop {array<string>} answers        -   
 * @prop {Object.<string, any>} gameResult            -    
 */
const INITIAL_STATE = Object.freeze({
  userName: ` `,
  lives: RULES.lives,
  time: RULES.gameTime,
  currentLevel: 0,
  answers: [], // ex.: [`CORRECT`, `CORRECT`, `WRONG`, `FAST`, `SLOW`]
  gameResult: {
    score: 0,
    success: false
  },
});

