/**
 * View  " "
 */

import {RULES} from "../../data/settings";
import AbstractView from "../../abstract-view";

export default class RulesView extends AbstractView {
  // language=HTML <header class="header">+++++++++++</header><section class="rules"><header class="header"></header>
  get template() {
    return `<section class="rules">
    <h2 class="rules__title"></h2>
    <ul class="rules__description">
        <li> ${RULES.levels}     
            <img class="rules__icon" src="img/icon-photo.png" width="32" height="31" alt="">  
            <img class="rules__icon" src="img/icon-paint.png" width="32" height="31" alt=""></li>
        <li>      .</li>
        <li>    ${RULES.gameTime} .</li>
        <li>    ${RULES.lives} .</li>
    </ul>
    <p class="rules__ready">?</p>
    <form class="rules__form">
        <input class="rules__input" type="text" placeholder=" " maxlength="20" minlength="3">
        <button class="rules__button  continue" type="submit" disabled>Go!</button>
    </form>
  </section>`;
  }

  onNextScreen() {
  }

  bind() {

    /**
     * :
     * 1)     (`.rules__input`).     minlength="3"  maxlength="20",    html,
     *    `Go`,       
     * 2)   Go (`.rules__button`)  -    
     */
    const formInput = this.element.querySelector(`.rules__input`);
    const buttonSubmit = this.element.querySelector(`.rules__button`);

    formInput.addEventListener(`input`, (evt) => {
      if (formInput.minLength <= evt.target.value.length && evt.target.value.length <= formInput.maxLength) {
        buttonSubmit.removeAttribute(`disabled`);
        buttonSubmit.removeAttribute(`style`);
      } else {
        buttonSubmit.setAttribute(`disabled`, `disabled`);
        formInput.style.borderColor = `red`;
      }
    });

    buttonSubmit.addEventListener(`click`, () => {
      this.onNextScreen(formInput.value);
    });
  }
}
